    display: inline-block;
    margin-bottom: 15px;
    width: fit-content;
    font-weight: 500;
    font-size: 24px;
    color: #ff3;

    + span {
      margin-left: 18px;
    }

    @include m($xl) {
      margin-bottom: 12px;
      font-size: 20px;
    }

    @include m($lg) {
      font-size: 16px;
    }
