    margin-bottom: 25px;
    font-weight: 600;
    font-size: 64px;
    line-height: 1.2;
    color: #fff;

    @include m(1600) {
      font-size: 48px;
    }

    @include m($xl) {
      font-size: 32px;
    }
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    width: fit-content;
    border-bottom: 1px solid transparent;
    color: #ff3;
    transition: border-bottom 0.2s;

    span {
      margin: 0;
      border-bottom: 1px solid transparent;
      font-weight: 400;
      font-size: 20px;
      line-height: 1.2;
      transition: text-decoration 0.2s;

      &:hover {
        text-decoration: underline;
      }

      @include m($xl) {
        font-size: 14px;
      }
    }

    img {
      display: block;
      margin-top: -2px;
      margin-left: 10px;
      width: 24px;
      height: 24px;
      transition: margin-left 0.2s;

      @include m($xl) {
        margin-top: -3px;
      }

      @include m($lg) {
        display: none;
      }
    }

    @include m($xl) {
      margin-bottom: 25px;
    }

    @include m($md) {
      margin-bottom: 65px;
    }
  }
}

.title {
  padding-bottom: 15px;
  border-bottom: 1px solid #d9dad9;

  h2,
  span {
    font-weight: 600;
    font-size: 40px;
    text-transform: uppercase;
    color: #fff;

    @include m($lg) {
      font-size: 24px;
    }
  }

  h2 {
    position: relative;
    padding-left: 378px;

    @include m($xl) {
      padding-left: 72px;
    }

    @include m($lg) {
      padding-left: 46px;
    }
  }

  span {
    position: absolute;
    left: 0;
    top: 0;
    display: block;
  }

  .container {
    @include m($lg) {
      padding: 0;
    }
  }

  @include m($lg) {
    margin: 0 17px;
    padding-bottom: 5px;
  }
}

.about {
  margin-bottom: 300px;

  &__info {
    margin: 50px 0 150px;
    padding-left: 378px;

    p:not(:last-child) {
      margin-bottom: 15px;
    }

    p {
      @include m($lg) {
        font-size: 18px;
      }

      @include m($md) {
        font-size: 16px;
      }

      @include m($sm) {
        font-size: 14px;
      }
    }

    @include m($xl) {
      margin: 50px 0 100px;
      padding-left: 0;
    }

    @include m($lg) {
      margin: 25px 0;
    }
  }

  &__image {
    img {
      width: 100%;
      object-fit: cover;

      @include m($xl) {
        height: 300px;
      }

      @include m($lg) {
        height: fit-content;
        min-height: 200px;
      }
    }
  }

  @include m($xl) {
    margin-bottom: 150px;
  }

  @include m($lg) {
    margin-bottom: 100px;
  }
}

.offer {
  margin-bottom: 300px;

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 150px auto 0;
    max-width: 1200px;

    li {
      position: relative;
      width: fit-content;
      height: 100%;
      overflow: hidden;

      p {
        position: absolute;
        left: 50%;
        top: 50%;
        width: fit-content;
        font-weight: 600;
        font-size: 36px;
        text-align: center;
        text-transform: uppercase;
        color: #fff;
        transform: translate(-50%, -50%);

        @include m($xl) {
          padding: 0 40px;
          font-size: 32px;
        }

        @include m($md) {
          padding: 0 30px;
          font-size: 26px;
        }

        @include m($sm) {
          padding: 0 5px;
          font-size: 16px;
        }
      }

      &:nth-child(-n+2) {
        @include m($lg) {
          margin-right: 18px;
        }
      }

      @include m($xl) {
        max-width: 226px;
      }
    }

    @include m($xl) {
      margin: 50px auto 0;
      max-width: 735px;
    }

    @include m($lg) {
      display: flex;
      justify-content: center;
      margin: 25px auto 0;
    }
  }

  @include m($xl) {
    margin-bottom: 150px;
  }

  @include m($lg) {
    margin-bottom: 100px;
  }
}

.mission {
  margin-bottom: 300px;

  &__list {
    margin-top: 50px;

    li {
      display: flex;

      img {
        margin-right: 32px;
        width: 100%;
        max-width: 346px;
        object-fit: cover;

        @include m($xl) {
          margin-right: 0;
          height: 300px;
          max-width: 100%;
        }

        @include m($lg) {
          height: fit-content;
          min-height: 150px;
        }
      }

      h4 {
        margin-bottom: 10px;
        font-weight: 500;
        font-size: 24px;
        color: #ff3;

        @include m($lg) {
          font-size: 20px;
        }

        @include m($md) {
          font-size: 18px;
        }

        @include m($sm) {
          font-size: 16px;
        }
      }

      &:not(:last-child) {
        margin-bottom: 100px;

        @include m($lg) {
          margin-bottom: 50px;
        }
      }

      @include m($xl) {
        flex-direction: column-reverse;
      }
    }

    p {
      @include m($lg) {
        font-size: 18px;
      }

      @include m($md) {
        font-size: 16px;
      }

      @include m($sm) {
        font-size: 14px;
      }
    }

    @include m($lg) {
      margin-top: 25px;
    }
  }

  &__inner {
    @include m($xl) {
      margin-bottom: 20px;
    }

    @include m($lg) {
      margin-bottom: 25px;
    }
  }

  @include m($xl) {
    margin-bottom: 150px;
  }

  @include m($lg) {
    margin-bottom: 100px;
  }
}

.contact {
  margin-bottom: 150px;

  &__info {
    display: flex;
    margin: 50px 0;
    padding-left: 378px;

    li {
      width: 100%;
      max-width: 346px;

      + li {
        margin-left: 32px;

        @include m($lg) {
          margin-left: 0;
        }
      }

      h4 {
        margin-bottom: 10px;
        font-weight: 500;
        font-size: 24px;
        color: #ff3;

        @include m($lg) {
          font-size: 20px;
        }

        @include m($md) {
          font-size: 18px;
        }

        @include m($sm) {
          font-size: 16px;
        }
      }

      a {
        color: #fff;

        @include m($lg) {
          font-size: 18px;
        }

        @include m($md) {
          font-size: 16px;
        }

        @include m($sm) {
          font-size: 14px;
        }
      }

      &:not(:last-child) {
        @include m($lg) {
          margin-bottom: 25px;
        }
      }

      &:nth-child(3) {
        a {
          display: inline-block;

          &:last-child {
            margin-left: 25px;
          }
        }
      }
    }

    @include m($xl) {
      padding-left: 0;
    }

    @include m($lg) {
      flex-direction: column;
      margin: 25px 0;
    }
  }

  &__map {
    img {
      width: 100%;
      object-fit: cover;
    }

    @include m($xl) {
      margin: 0 -40px;

      img {
        height: 300px;
      }
    }

    @include m($lg) {
      margin: 0 -17px;

      img {
        height: fit-content;
        min-height: 200px;
      }
    }
  }

  @include m($xl) {
    margin-bottom: 100px;
  }

  @include m($lg) {
    margin-bottom: 50px;
  }
}